:host([code-theme="dark"]) code[class*="language-"],
:host([code-theme="dark"]) pre[class*="language-"] {
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  color: #D2D2D2;
  background: #3C3F42;
  font-family: pfe-var(font-family--code);
  font-size: 1em;
  line-height: 1.5em;
  tab-size: 4;
  hyphens: none;
}

:host([code-theme="dark"]) code[class*="language-"]::selection,
:host([code-theme="dark"]) pre[class*="language-"]::selection,
:host([code-theme="dark"]) code[class*="language-"] ::selection,
:host([code-theme="dark"]) pre[class*="language-"] ::selection {
  background: #363636;
}

:host([code-theme="dark"]) :not(pre) > code[class*="language-"] {
  white-space: normal;
  border-radius: 0.2em;
  padding: 0.1em;
}

:host([code-theme="dark"]) pre[class*="language-"] {
  overflow: auto;
  position: relative;
  margin: 0.5em 0;
  padding: 1.25em 1em;
}

:host([code-theme="dark"]) pre[class*="language-"].line-numbers {
  position: relative;
  padding-left: 3.8em;
  counter-reset: linenumber;
}

:host([code-theme="dark"]) .language-css > code,
:host([code-theme="dark"]) .language-sass > code,
:host([code-theme="dark"]) .language-scss > code {
  color: #ACE12E;
}

:host([code-theme="dark"]) [class*="language-"] .namespace {
  opacity: 0.7;
}

